import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { TranslateService } from '@ngx-translate/core';
import { HttpProvider } from '../../providers/http/http';
import { UtilsProvider } from '../../providers/utils/utils';
import { CommandsProvider } from '../../providers/commands/commands';

/**
 * Generated class for the CheckStatePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-check-state',
  templateUrl: 'check-state.html',
})
export class CheckStatePage {

  public message: string;
  public listaCheck: string;
  
  constructor(public navCtrl: NavController, public navParams: NavParams, private storage: Storage, private translateService: TranslateService, public utils: UtilsProvider,
              public http: HttpProvider, public command: CommandsProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CheckStatePage');
  }

  check(){
    this.navCtrl.push("ChequePage");
  }

  estadoCuent(){
    this.navCtrl.push("EstadocuentaPage");
    
  }

  logout(){
    this.utils.presentConfirm("LoginPage");
    //this.utils.logout();
  }


  //Metodo para hacer el pago de pay bills
  private cargarCheques(){

    //var cmd = "E2LSMR6081";
    var command :string[] =  this.utils.getCommands(this.command.E2LSMR6081);

    var URL=  command[0]+ '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+ '&E2EW=%BA'+ 
    this.utils.separator +'%BA'+ this.utils.separator +'0%BA'+ this.utils.separator +'%BA'+this.utils.separator;

    this.http.callServer(URL).then(res => {      
      this.message = res.message;
  
        if (this.message != '' && typeof this.message!=="undefined") {
          this.utils.alertServidor(this.message);
        } else {
          this.listaCheck = res.lista1;
          //alert("Lista cheques"+ this.listaCheck);
        }
      },
        error => {
          console.log(error);
        });
  }

}



